<template>
  <button type="button" @click="$router.push('/write')">글쓰기</button>
    <div class="board-list">
        <table id="list">
            <thead>
                <tr>
                    <th>번호</th>
                    <th>제목</th>
                    <th>날짜</th> 
                    <th>작성자</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="list in blist" :key="list.bseq">
                    <td>{{ list.bseq }}</td>
                    <router-link :to="`/content?bseq=${list.bseq}`">{{ list.btitle }}</router-link>
                    <td>{{ list.bdate }}</td>
                    <td>{{ list.bauthor }}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
  
<script>


import axios from 'axios';

export default {
  data() {
    return {
      blist: []
    };
  },
  created() {
    axios.get('http://localhost:8082/') // 백엔드 API 엔드포인트에 맞게 수정
      .then(response => {
        this.blist = response.data;
      })
      .catch(error => {
        console.error(error);
      });
  }
};

</script>